@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(fonts/diagram/segoeui.css);
@import url(fonts/stix/stix.css);

.ms-editor {
    position: relative;
    z-index: 20;
    color: #1A9FFF;
    font-family: sans-serif;
}

.ms-editor canvas,
.ms-editor svg {
    z-index: 15;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

.ms-editor canvas.ms-rendering-canvas,
.ms-editor svg {
    z-index: 10;
    pointer-events: none;
    background: repeating-linear-gradient(180deg, #F5F6F7, #F5F6F7 1px, transparent 1px, transparent 18px), repeating-linear-gradient(90deg, #F5F6F7, #F5F6F7 1px, transparent 1px, transparent 18px);
}

.ms-editor .loader {
    z-index: 30;
    position: absolute;
    width: 120px;
    height: 120px;
    top: calc(50% - 60px);
    left: calc(50% - 60px);
    border: 16px solid #F5F6F7;
    border-radius: 50%;
    border-top-color: #1A9FFF;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
}

.ms-editor .error-msg {
    z-index: 25;
    position: absolute;
    width: 200px;
    height: 200px;
    top: calc(50% - 100px);
    left: calc(50% - 100px);
    font-size: 16px;
    text-align: center;
    word-wrap: break-word;
}

.ms-editor .error-msg::before {
    content: url(data:image/svg+xml;base64,);
}

.withfadeout {
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -ms-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}

#prompter {
    position: fixed;
    z-index: 40;
    font-size: 16px;
}

.prompter-text-container {
    background-color: white;
    height: 48px;
    line-height: 48px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    display: block;
    text-align: left;
    border-bottom: 1px solid lightgray;
    position: fixed;
    z-index: 30;
    color: #808080;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.prompter-text-container > div > span {
    cursor: pointer;
}

#prompter-text {
    margin-left: 12px;
}

.candidates {
    color: black;
    padding: 0 10px;
    text-align: center;
    line-height: 30px;
    border: 1px solid #CED5D9;
    border-radius: 3px;
    position: fixed;
    background-color: #ffffff;
    z-index: 100;
}

.candidates > span {
    cursor: pointer;
}

.bold-candidate {
    font-weight: bold;
}

.paragraph-icon {
    padding: 0 18px;
    border: 1px solid lightgrey;
    font-weight: bold;
    font-size: large;
    user-select: none;
    position: fixed;
    z-index: 31;
    height: 48px;
    line-height: 48px;
    background-color: white;
    color: #808080;
}

#ellipsis {
    cursor: pointer;
    border-bottom: 1px solid lightgray;
    position: fixed;
    z-index: 31;
    height: 48px;
    line-height: 38px;
    padding:0 8px;
    font-weight: bold;
    font-size: x-large;
    background-color: white;
    color: #808080;
    user-select: none;
}

.options {
    margin-right: 12px;
    padding: 0 10px;
    text-align: center;
    line-height: 30px;
    border: 1px solid #CED5D9;
    border-radius: 3px;
    position: fixed;
    background-color: #ffffff;
    z-index: 100;
}

.options-label-button {
    cursor: pointer;
    box-sizing: border-box;
    color: #1A9FFF;
    background: transparent;
    border: none;
    padding: 0;
    margin: 0;
    height: 40px;
    outline: none;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes rotate-plan {
    0% {
        transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    }
    25% {
        transform: perspective(120px) rotateX(0deg) rotateY(-180deg);
    }
    50% {
        transform: perspective(120px) rotateX(-180deg) rotateY(-180deg);
    }
    75% {
        transform: perspective(120px) rotateX(-180deg) rotateY(0deg);
    }
    100% {
        transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    }
}

@-webkit-keyframes rotate-plan {
    0% {
        -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg)
    }
    25% {
        -webkit-transform: perspective(120px) rotateX(180deg) rotateY(0deg)
    }
    50% {
        -webkit-transform: perspective(120px) rotateX(180deg) rotateY(180deg)
    }
    75% {
        -webkit-transform: perspective(120px) rotateX(0deg) rotateY(180deg)
    }
    100% {
        -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg)
    }
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}
